<template>
	<div class="welcomeAll">
		
		<!--//偏移-->
		<div class="welcomee">
			{{Hello}} {{$route.params.name}}({{$route.params.nikeName}})
		</div>
		<div class="clear"></div>
		<div class="right">
			<div class="write">写博客</div>
			<div class="tj">
				<div class="panel panel-success">
				  	<div class="panel-heading">
				    	<h3 class="panel-title">统计</h3>
				 	</div>
				    <div class="panel-body">
				   	 	浏览量（123456）
				  	</div>
				  	<div class="panel-body">
				   	 	点赞数（123456）
				  	</div>
				  	<div class="panel-body">
				   	 	评论数（123456)
				  	</div>
				</div>
			</div>
			<div class="gl">
				<div class="panel panel-success">
				  	<div class="panel-heading">
				    	<h3 class="panel-title">管理</h3>
				 	</div>
				    <div class="panel-body">
				   	 	博客管理
				  	</div>
				  	<div class="panel-body">
				   	 	资料管理
				  	</div>
				  	<div class="panel-body">
				   	 	评论管理
				  	</div>
				  	<div class="panel-body">
				   	 	账号管理
				  	</div>
				</div>
			</div>
		</div>
		
		<div class="left">
			<div class="panel panel-success">
			  <!-- Default panel contents -->
				  	<div class="panel-heading">我的好友</div>
				 	 	<div class="panel-body">
				 	  	 	<p>...<button type="button" class="right btn btn-default " @click="addList">添加好友</button></p>
				 	 	</div>
			
			  <!-- List group -->
				  <ul class="list-group" v-for="(item,index) in list">
				    <li class="list-group-item">{{item}}<button @click="deleteList(index)" class="right btn btn-default ">删除</button></li>
				  </ul>
			</div>
		
		</div>
	
		<div class="insidee" v-if="add">
				<div class="Top">
					<span class="left">click</span>
					<span class="right" @click="add=false">取消</span>
				</div>
				<div class="form-group">
					    <label for="inputEmail3" class="col-sm-3 control-label content">
					    	请输入好友姓名<br />
					    <div class="col-sm-9" style="position: relative;top: 20px;">
					      <input type="text" class="form-control content"  v-model="addName">
					    </div>
					    <button 
					    	type="button" 
					    	class="btn-success" 
					    	style="position: relative;
					    	top: 40px;
					    	width: 120px;"
					    	@click="confirmAdd"
					    	>确定</button>
					    </label>
					    
				</div>
			
		</div>
		
	</div>
</template>

<script>
	export default {
		data () {
			return {
				Hello : "",
				list:["Marry",'Damon','Daisy','Cat','Y'],
				add:false,
				addName:''
			}
		},
		mounted ()	{
			this.$nextTick(function () {
			    this.HelloDate();// Code that will run only after the
			    // entire view has been rendered
			  })
		},
		methods : {
			HelloDate () {
				var date = new Date();
				if(date.getHours()>=6&&date.getHours()<=18){
					this.Hello = "早上好！"
				}else{
					this.Hello =  "晚上好！"
				}
			},
			deleteList (indexx) {
				this.list.splice(indexx,1)
			},
			addList () {
				this.add = true
			},
			confirmAdd () {
				this.list.push(this.addName)
				this.add = false
				this.addName = ''
			}
		}
	}
</script>

<style>
</style>


