
<template>
	<div>
		<content>
			<div class="backimg"></div>
			<form class="form-horizontal">
			  <div class="form-group ">
			    <label for="inputEmail3" class="col-sm-3 control-label">账号</label>
			    <div class="col-sm-9 form-control-static">
			      123456
			    </div>
			  </div>
				
				<div v-if="error.length" class="form-group  " style="width: 500px;position: relative;left: 100px;color: red;font-weight: 700;">
					<div v-for="error in error" class="error">{{error}}</div>
				</div>
			  <div class="form-group">
			  	
				
			    <label for="inputEmail3" class="col-sm-3 control-label" >昵称</label>
			    <div class="col-sm-9">
			      <input 
			      	type="text" 
			      	class="form-control" 
			      	id="inputEmail3" 
			      	placeholder="请输入您的昵称" 
			      	v-model="nikeName"
			      	:class="{'check':check1}"
			      	>
			    </div>
			  </div>
			  <div class="form-group">
			    <label for="inputEmail3" class="col-sm-3 control-label">姓名</label>
			    <div class="col-sm-9">
			      <input type="text" class="form-control" id="inputEmail3" placeholder="请输入您的姓名" v-model="name" :class="{'check':check2}">
			    </div>
			  </div>
			  
			  <div class="form-group">
			    <label for="inputEmail3" class="col-sm-3 control-label">电话</label>
			    <div class="col-sm-9">
			      <input type="text" class="form-control" id="inputEmail3" placeholder="请输入您的电话" v-model="tel">
			    </div>
			  </div>
			  <div class="form-group">
			    <label for="inputEmail3" class="col-sm-3 control-label">邮箱</label>
			    <div class="col-sm-9">
			      <input type="email" class="form-control" id="inputEmail3" placeholder="请输入您的邮箱" v-model="mail">
			    </div>
			  </div>
			  <div class="form-group">
			    <label for="inputPassword3" class="col-sm-3 control-label">Password</label>
			    <div class="col-sm-9">
			      <input type="password" class="form-control" id="inputPassword3" placeholder="Password" v-model="passWord">
			    </div>
			  </div>
			  <div class="form-group">
			    <div class="col-sm-offset-2 col-sm-10">
			      <div class="checkbox">
			        <label>
			          <input type="checkbox"> Remember me
			        </label>
			      </div>
			    </div>
			  </div>
			  <div class="form-group">
			    <div class="col-sm-offset-2 col-sm-10" >
			      <router-link :to="{name:'welcome',params:{name:name,nikeName:nikeName}}">
				      <button type="submit" class="btn btn-default" style="padding: 5px 20px;" @click="save">保存</button>
			      </router-link>
			       <button  class="btn btn-danger" style="position: relative;left: 50px;padding: 5px 20px;">取消</button>
			    </div>
			  </div>
			</form>
		</content>
		
</div>		

		</template>

<script>
export default {
	data () {
		return {
			message: "aaa",
			Center: false,
			newMessage:"",
			nikeName:"",
			name: "",
			tel:"",
			mail: "",
			passWord:"",
			check1:false,
			check2:false,
			error:[]
		}	
	},
	methods:{
		appCenter () {
			this.Center=true;
		},
		save () {
			if(!this.nikeName){
				this.error.push("请输入昵称")
			}
			if(!this.name){
				this.error.push("请输入姓名")
			}
		}
	},
	watch : {
		name () {
			if(name){
				this.checks = true
			}
		}
	}
}
</script>

<style>
	
</style>


