import Vue from 'vue'
import Router from 'vue-router'

import Log from "@/components/log"
import welcome from "@/components/welcome"



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Log',
      component: Log
    },
    {
    	path: '/welcome/:name/:nikeName',
    	name : "welcome",
    	component: welcome
    }
  ]
})



